.header {
	padding: 120px  120px;
	min-height: 695px;

	display: flex;
	justify-content: center;
	align-items: center;

	background-color: var(--header-bg);
	background-image: url('./../../img/og-devs.jpg');
	background-repeat: no-repeat;
	background-size: auto;
	background-position: center center;
	width: 100vw;


	color: var(--header-text);
	text-align: center;
}

.header__wrapper {
	padding: 0 15px;
	max-width: 660px;
}

.header__title {
	margin-bottom: 20px;
	font-size: 40px;
	font-weight: 700;
	line-height: 1.4;
}

.header__title strong {
	font-size: 60px;
	font-weight: 700;
}

.header__title em {
	font-style: normal;
	color: var(--accent);
}

.header__text {
	margin-bottom: 40px;
	font-size: 18px;
	line-height: 1.333;
}

.header__text p + p {
	margin-top: 0.5em;
}
.btn_white{
	background-color:#fff;

  }
  .btn{
	display:block;
	width:280px;
	margin:0 auto;
	color: black;
	
	
}
